@media screen and (max-width: 1280px) {
  .container {
    max-width: 768px;
  }

  body.lock {
    overflow: hidden;
  }

  .header-body {
    height: 50px;
  }

  .nav-list {
    display: block;
  }

  .hidden {
    width: 100%;
    justify-content: space-evenly;
    display: flex;
  }

  .header-burger {
    z-index: 3;
    width: 30px;
    height: 20px;
    margin: 0 0 0 20px;
    display: block;
    position: relative;
  }

  .header-burger span {
    width: 100%;
    height: 2px;
    background-color: #fffcf6;
    position: absolute;
    top: 9px;
    left: 0;
  }

  .nav-list {
    padding-bottom: 30px;
  }

  .header-burger:before, .header-burger:after {
    content: "";
    width: 100%;
    height: 2px;
    background-color: #fffcf6;
    list-style: none;
    display: block;
    position: absolute;
    left: 0;
  }

  .nav-ref {
    margin-left: 20px;
  }

  .header-burger:before {
    top: 0;
  }

  .header-burger:after {
    bottom: 0;
  }

  .header-menu {
    width: 100%;
    height: 100%;
    background-color: #313f38;
    padding-top: 80px;
    position: fixed;
    top: -100%;
    left: 0;
    overflow: auto;
  }

  .header-menu.active {
    top: 0;
  }

  .header-burger.active:before {
    top: 9px;
    transform: rotate(45deg);
  }

  .header-burger.active:after {
    top: 9px;
    transform: rotate(-45deg);
  }

  .header-burger.active span {
    transform: scale(0);
  }

  .gallery-container {
    position: relative;
  }

  .nav-item:not(:last-child) {
    margin-bottom: 40px;
  }

  .header-num {
    justify-content: center;
    display: flex;
  }

  .header-socials-list {
    width: 100%;
    justify-content: space-evenly;
  }
}

